<template>
    <div>
        <div id="map" class="map"></div>
        <layerPanel :child_msg="'Layer-Panel'"/>
        <metadataPanel :child_msg="'Metadata-Panel'"/>
        <timePanel :child_msg="'Time-Panel'"/>
        <basemapPanel 
            :child_msg="'Basemap-Panel'"
            :data="basemap_settings"
        />
        <slidePanel :child_msg="'Slide-Panel'"/>
        <plotPanel :child_msg="'Plot-Panel'"/>
        <div id="popup" class="popup ol-unselectable ol-control"></div>
    </div>

</template>

<script>
// Settings
import {settings} from '@/assets/js/settings'
const basemap_settings = settings.basemap
// Components
import LayerPanel from './panels/LayerPanel.vue'
import MetadataPanel from './panels/MetadataPanel.vue'
import TimePanel from './panels/TimePanel.vue'
import BasemapPanel from './panels/BasemapPanel.vue'
import PlotPanel from './panels/PlotPanel.vue'
import SlidePanel from './panels/SlidePanel.vue'
// Handle the request
import axios from "axios"
// XML parser
import WMSCapabilities from 'ol/format/WMSCapabilities';
const parser = new WMSCapabilities();

export default {
    name: "WebGISContainer",
    data () {
        return {
            basemap_settings: basemap_settings,
        }
    },
    components: {
        layerPanel: LayerPanel,
        metadataPanel: MetadataPanel,
        timePanel: TimePanel,
        basemapPanel: BasemapPanel,
        plotPanel: PlotPanel,
        slidePanel: SlidePanel,
    },
    mounted() {
        axios.get("http://127.0.0.1/geoserver/ows?service=wms&version=1.3.0&request=GetCapabilities")
            .then(response => {
                let test = parser.read(response.data)
                console.log(test.Capability.Layer.Layer)
            })
            .catch(err => console.log("There was an issue retrieving the service layers"))
    }
}
</script>

<style lang="scss">
@import 'css/style.css';
</style>